.App {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.title {
  font-size: 30px;
  color: grey;
  margin: 8px;
}

.words-table {
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  padding: 6px;
  width: 400px;
  margin: 8px auto;
  background-color: white;
  border-radius: 5px;
  box-shadow: -3px -3px 7px #ffffff73, 2px 2px 5px rgb(94 104 121 / 29%);
}

.Row {
  display: flex;
}

.letter {
  box-sizing: border-box;
  /* border: 2px solid gray; */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  width: 60px;
  margin: 6px;
  font-size: 32px;
  text-transform: uppercase;
  height: 60px;
  padding: 10px;
  border: none;
  outline: none;
  background: white;
  box-shadow: inset -3px -3px 7px #ffffff73,
              inset 2px 2px 5px rgba(94,104,121,0.288);
  /* background: #131419; */
  /* box-shadow: 2px 2px 5px rgba(0,0,0,0.5),
              -3px -3px 7px rgba(255,255,255,0.05); */
  margin: 10px;
  color: grey;
  /* color: #c7c7c7; */
  font-size: 20px;
  border-radius: 5px;
  cursor: default;
}

.highlighted {
  border: 3px solid #839fff;
}

.correct-letter {
  background-color: rgb(231 231 3 / 53%) !important; 
}

.correct-position {
  background-color: rgb(162 235 162) !important;
}

.letter-not-in-word {
  background-color: rgb(217 216 216) !important;
}

.hidden-letter {
  visibility: hidden;
}

.App .react-simple-keyboard {
  display: flex;
  justify-content: center;
  min-width: 400px;
  max-width: 1000px;
  font-family: 'Nunito Sans' !important;
  width: auto;
  overflow: visible;
  margin: 20px 0;
  padding: 12px;
  border-radius: 10px;
  background: #dde1e7;
  box-shadow: -3px -3px 7px #ffffff73,
               2px 2px 5px rgba(94,104,121,0.288);
  /* background: #131419; */
  /* box-shadow: 2px 2px 5px rgba(0,0,0,0.5),
              -3px -3px 7px rgba(255,255,255,0.05); */
}

.App .react-simple-keyboard .hg-button {
    align-items: center;
    background: #fff;
    border-bottom: 1px solid #b5b5b5;
    border-radius: 5px;
    box-shadow: 0 0 3px -1px rgb(0 0 0 / 30%);
    box-sizing: border-box;
    cursor: pointer;
    display: flex;
    height: 40px;
    justify-content: center;
    padding: 5px;
    -webkit-tap-highlight-color: white;
    height: 50px;
    min-width: 50px;
    padding: 10px;
    border: none;
    outline: none;
    background: #dde1e7;
    box-shadow: -3px -3px 7px #ffffff73, 2px 2px 5px rgb(94 104 121 / 29%);
    margin: 10px;
    color: grey;
    font-size: 20px;
    border-radius: 8px;
    cursor: pointer;
    background: linear-gradient(145deg, #ecf1f6, #c6cfd9);
}

@media only screen and (max-width: 750px) {
  .App .react-simple-keyboard {
    max-width: 1000px;
    padding: 0;
    border-radius: 10px;
    background: none;
    box-shadow: none;
  }
  
  .App .react-simple-keyboard .hg-button {
      padding: 5px;
      min-height: 30px;
      min-width: 30px;
      padding: 8px;
      margin: 4px;
      font-size: 20px;
      padding: 5px;
      /* min-height: 30px; */
      /* max-width: 26px; */
      width: 29px;
      /* min-width: 30px; */
      min-width: unset;
      /* padding: 8px; */
      margin: 4px;
      font-size: 20px;
  }

  .words-table {
    width: auto;
  }

  .letter {
    width: 40px;
    height: 40px;
    margin: 6px;
  }
}
