body {
  margin: 0;
  font-family: 'Nunito Sans' !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #d0def4;
  height: 100%;
  overflow-y: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#__react-alert__ > div {
  pointer-events: all !important;
  bottom: 0;
}

.alert {
  display: flex;
  width: 300px;
  flex-direction: column;
  align-items: center;
  background-color: #dde1e6;
  padding: 20px;
  box-shadow: -3px -3px 7px #ffffff73, 2px 2px 5px rgb(94 104 121 / 29%);
  border: none;
  border-radius: 5px;
  color: grey;
  font-size: 30px;
  color: #75a3e2;
  font-family: 'Nunito Sans' !important;
  font-size: 30px;
}

.play-again-button {
  box-shadow: -3px -3px 7px #ffffff73, 2px 2px 5px rgb(94 104 121 / 29%);
  border: none;
  border-radius: 5px;
  font-family: 'Nunito Sans' !important;
  color: grey;
  font-size: 20px;
  margin: 10px;
  padding: 10px;
  color: white;
  background-color: #76a4e2;
  width: 200px;
  cursor: pointer;
  border-radius: 5px;
}
